.card{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border: 2px solid #119fbfd3;
    border-radius: 10px;
    margin: 10px;
}
.card:hover{
    background-color: #119fbf3f;
    padding: 10px;
}

.card .h2{
    font-size: 1.8rem;
    color: #023059;
    text-align: center;
    padding: 10px;
}
.ico{
    font-size: 4rem;
    color: #F2A413;  
}
.card .p{
    font-size: 1.1rem;
    color: #023059;
    padding: 10px;
    text-align: center;
}