.nav{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #023059;
    color:azure;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.596);
    position: sticky;
    top: 0;
     
}
.nav h1{
    font-size: 2.5rem;
    font-weight: bold;
    font-family: 'Noto Serif', serif;
}
.nav p{
    font-size: .9rem;
}
.nav .divlogo{
    display: flex;
}
.nav div img{
    width: 15%;
}
.nav .div a{
  
   text-decoration: none;
   color: aliceblue;
   font-size: 1.3rem;
   font-weight: 500;
   transition: .6s;
   cursor: pointer;
   margin-right: 20px;
}
.nav .div a:hover{
    padding: .2rem;
    background-color: #EEE400;
    color: #035AA6;
    border-radius: 5px;
}

.nav p{
    text-align: center;
}
.fa{
    display: none;
}
