
.centra{
    width: 1200px;
    margin:3rem auto;
    display: flex;
    flex-direction: column;
    flex-direction: column;
    align-items: baseline;
    min-height: 65%;
    flex-wrap: wrap;
    align-content: center;
}
.centra h1{
    color: #023059;
    font-size: 3rem;
    margin: 25px 0px;
}
.centra label{
    font-size: 1.1rem;
    font-weight: 600;
    color: #081f5fd3;
}
.centra input{
    width: 300%;
    padding: .5rem;
}
.centra button{
    background-color: #023059;
    color: aliceblue;
    padding: 4px;
}
.centra button:hover{
    background-color:#EEE400 ;
    color: #023059;
    padding: 5px;
}

