.footer{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #023059;
    color:azure;
   
}
.footer .social{
   margin: 0px auto;
   text-align: center;
}
.footer .social svg{
    background-color: #369612;
    color: aliceblue;
    margin: 0px 10px;
    transition: .6s;
    font-size: 4rem;
    border-radius: 10%;
}
.footer .social svg:hover{
    color:#369612;
    color: aliceblue;
    font-size: 4.3rem;
}
.footer a{
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: bold;
    color:#EEE400;
    cursor: pointer;
}
